<template>
  <div class="list-content">
    <div class="product-list">
      <div class="product-list-main">
        <div class="list-rowr" v-for="item in listdata">
          <div class="thumbnail">
            <div class="pro-img-box">
              <img :src=" './static/images/' + item.cate + '/' + item.img"/>
            </div>
            <div class="product-name">
              <a>
                <h2>{{item.comment}}</h2>
              </a>
            </div>
            <div class="product-price">
              <p>{{item.cost}}</p>
            </div>
            <div class="product-from">
              <p>购入方式：{{item.from }}</p>
            </div>
            <div class="">
              <p>购入日期：{{item.buydate}}</p>
            </div>
            <div v-if="item.status != 'using'">
              <p>{{item.status + '：'+ item.offdate}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import listdata from '../data/data1.js';
import '../../static/css/list.css';
export default {
  name: 'list',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      listdata
    }
  }
}
</script>

